.home {
    .bg-image {
        background: url('../images/img2.png') no-repeat top center;
        background-size: cover;
        height: 500px;
    }
    .product-title {
        margin: 10px 0;
    }
    .no-padding {
        padding-left: 0;
        padding-right: 0;
    }
    .recommend {
        background: #eee;
        padding: 50px 0;
    }
    .product {
        position: relative;
        .product-rank {
            background: #7b338d;
            position: absolute;
            width: 57px;
            height: 57px;
            top: 0;
            left: 0;
            color: #fff;
            line-height: 57px;
            text-align: center;
            font-size: 28px;
        }
    }
}
