.product {
    .has-child {
        .fa-undefined {
            display: none;
        }
    }
    .product-list-head {
        margin-bottom: 30px;
    }
    .filter {
        display: flex;
        font-size: 16px;
        .page-filter {
            margin-left: 45px;
        }
    }
    .product-box {
        margin-bottom: 15px;
    }
    .product-title {
        padding: 15px 7px;
    }
    .product-link {
        color: #222;
    }
    .product-info {
        padding: 15px 7px;
        display: flex;
    }
    .product-price {
        color: #ff0000;
        font-weight: 600;
        margin-right: 15px;
    }
    .product-old-price {
        color: #aaa;
        text-decoration-line: line-through;
    }

    .has-child {
        .fa-undefined {
            display: none;
        }
    }
    @media screen and (min-width: 576px) {
        .left-menu-template {
            grid-gap: 60px;
            grid-template-columns: 20% auto;
        }
    }

    .product-upper {
        display: grid;
        grid-template-columns: 570px auto;
        grid-column-gap: 44px;
    }
    .product-img {
        margin-bottom: 24px;
    }
    .product-title {
        margin-bottom: 20px;
    }
    .product-intro {
        font-size: 21px;
        color: #434343;
        margin-bottom: 20px;
    }
    .product-price {
        font-size: 36px;
        color: #f00;
        margin-bottom: 20px;
    }
    .product-spec,
    .product-qty {
        width: 300px;
        max-width: 100%;
        .label {
            font-size: 21px;
            color: #959595;
        }
        margin-bottom: 20px;
    }
    .product-spec-btn {
        border: 1px solid#ccc;
        border-radius: 5px;
        display: inline-block;
        background: transparent;
        padding: 7px 15px;
        & + .product-spec-btn {
            margin-left: 7px;
        }
    }
    .product-btn {
        margin-bottom: 20px;
        .add-cart {
            background: #7c338e;
            border-color: #7c338e;
        }
        .buy-now {
            background: #f19ec2;
            border-color: #f19ec2;
        }
        .btn {
            color: #fff;
            font-size: 21px;
        }
    }
    .product-share {
        text-align: center;
        margin-bottom: 20px;
        .btn {
            padding: 0 5px;
            &:hover,
            &:active {
                background: transparent;
            }
        }
    }
    .favorite {
        /* Required Styling */

        label input[type="checkbox"] {
            display: none;
        }

        .custom-checkbox {
            margin-left: 2em;
            position: relative;
            cursor: pointer;
        }

        .custom-checkbox .bi {
            position: absolute;
            left: -27px;
            font-size: 1.25em;
        }

        .custom-checkbox .bi-heart-fill {
            opacity: 0;
            transition: opacity 0.2s ease-in-out;
        }

        .custom-checkbox:hover .bi-heart-fill {
            opacity: 0.5;
        }

        .custom-checkbox input[type="checkbox"]:checked ~ .bi-heart-fill {
            opacity: 1;
            color: #f00;
        }
    }
    .social {
        font-size: 21px;
        line-height: 40px;
    }
    .tab-content {
        padding: 15px;
    }

    @media screen and (min-width: 1200px) {
        .product-container {
            width: 1200px;
            margin: 30px auto;
        }
    }
}
