.member {
    .member-box {
        padding: 25px;
        text-align: center;
        box-shadow: 0px 0px 6px 2px rgba(127, 127, 127, 0.4);
        margin-bottom: 30px;
        i {
            color: #de81a6;
            margin-right: 6px;
        }
    }
    .text-highlight {
        color: #f00;
        font-size: 24px;
        font-weight: bold;
        padding: 0 7px;
    }
    .has-child {
        .fa-undefined {
            display: none;
        }
    }
    .table-grey {
      background: #efefef;
      > th {
        background: #efefef;
      }
    }
    .review-icon {
      i {
        color: gold;
      }
    }
    .recommend-title {
      color: #7c328f;
    }
    .desc {
      margin: 7px auto;
      padding: 10px 0;
      border-top: 1px solid #aaa;
      border-bottom: 1px solid #aaa;
      width: 350px;
    }
    .recommend-link {
      display: grid;
      grid-template-columns: auto 100px;
      margin-bottom: 15px;
    }

    // wishlist
    .product-box {
      margin-bottom: 15px;
  }
  .wishlist-close {
    border: 0;
    background: #fff;
    border-radius: 50%;
    position:absolute;
    top: -100px;
    right: 10px;
    padding: 0;
    font-size: 20px;
    height: 26px;
    z-index: 10;
  }
  .product-title {
      padding: 15px 7px;
  }
  .product-link {
      color: #222;
      position: relative;
  }
  .product-info {
      padding: 15px 7px;
      display: flex;
  }
  .product-old-price {
      color: #aaa;
  }

  // tab style
  .project-tab {
    padding: 10%;
    margin-top: -8%;
  }
  .project-tab #tabs{
      background: #007b5e;
      color: #eee;
  }
  .project-tab #tabs h6.section-title{
      color: #eee;
  }
  .project-tab #tabs .nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active {
      color: #7c328f;
      background-color: transparent;
      border-color: transparent transparent #f3f3f3;
      border-bottom: 3px solid !important;
      font-size: 16px;
      font-weight: bold;
  }
  .project-tab .nav-link {
      border: 1px solid transparent;
      border-top-left-radius: .25rem;
      border-top-right-radius: .25rem;
      color: #7c328f;
      font-size: 16px;
      font-weight: 600;
  }
  .project-tab .nav-link:hover {
      border: none;
  }
  .project-tab thead{
      background: #f3f3f3;
      color: #333;
  }
  .project-tab a{
      text-decoration: none;
      color: #333;
      font-weight: 600;
  }

  .form-border-bottom {
    border-bottom: 1px solid #efefef;
    padding-bottom: 15px;
  }
}
