.container-xxl {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 1400px) {
  .container-xxl, .container-xl, .container-lg, .container-md, .container-sm, .container {
      max-width: 1360px;
  }
}

.navbar-toggler {
  background-color: #7d348e;
  margin: 35px 6px 5px;
  i {
    color: #fff;
  }
}

#mobi-menu {
  position: fixed;
  background-color: #DE7FA6;
  color: #fff !important;
  width: 100%;
  height: 100%;
  z-index: 10;
  padding: 80px 50px 50px;
  #close-mobi-menu {
    position: absolute;
    right: 40px;
    top: 30px;
  }
  ul > li > a {
    color: #fff !important;
  }
  .main-menu {
    margin-top: 20px;
    ul > li {
      padding: 10px 0;
      border-bottom: 1px solid #fff;
      &:first-child {
        border-top: 1px solid #fff;
      }
      a {
        position: relative;
        i.bi {
          position: absolute;
          right: 10px;
        }
      }
    }
  }
  .mobile-top-menu {
    display: flex;
    padding: 0;
    li {
      list-style-type: none;
      padding: 5px 8px;
      i.bi {
        font-size: 18px;
      }
      .search {
        position: relative;
        input {
          border: 0;
          border-radius: 30px;
        }
        i.bi {
          position: absolute;
          color: #aaa;
          right: 10px;
          top: 2px;
          font-size: 14px;
        }
      }
    }
  }
  .mobile-bottom-menu {
    display: flex;
    justify-content: space-around;
    padding: 0;
    flex-wrap: wrap;
    margin-top: 20px;
    li {
      list-style-type: none;
      flex-basis: 33%;
      padding: 10px;
      a {
        padding: 15px 10;
      }
    }
  }
}

#close-mobi-menu {
  color: #fff;
}

@media (max-width: 576px) {
  .mobi-menu-style {
    font-size: 0.75rem;
    width: 100%;
  }
}

@media (min-width: 576px) {
  #mobi-menu {
    display: none;
  }
}

.no-padding {
    padding-left: 0;
    padding-right: 0;
  }
  .text-theme {
    color: #7c328f;
  }
  .social-btn {
    padding: 0.5rem;
  }
  .footer {
    background: #f1e6ff;
  }
  .top-left, .top-right {
    .nav-item {
      font-size: 14px;
    }
  }
  .icon-style {
    background: #eee;
    padding: 3px;
    position: relative;
    + .icon-style {
      &::before {
        content: '|';
        position: absolute;
        top:10px;
        left:0;
        color: #aaa;
      }
    }
  }

  // layout
  @media screen and (min-width: 576px) {
    .left-menu-template {
      display: grid;
      grid-gap: 60px;
      grid-template-columns: 20% auto;
    }
  }
  // layoutend

  // sidebar

  @keyframes swing {
    0% {
      transform: rotate(0deg);
    }
    10% {
      transform: rotate(10deg);
    }
    30% {
      transform: rotate(0deg);
    }
    40% {
      transform: rotate(-10deg);
    }
    50% {
      transform: rotate(0deg);
    }
    60% {
      transform: rotate(5deg);
    }
    70% {
      transform: rotate(0deg);
    }
    80% {
      transform: rotate(-5deg);
    }
    100% {
      transform: rotate(0deg);
    }
  }
  
  @keyframes sonar {
    0% {
      transform: scale(0.9);
      opacity: 1;
    }
    100% {
      transform: scale(2);
      opacity: 0;
    }
  }
  body {
    font-size: 0.9rem;
  }
  .page-wrapper .sidebar-wrapper,
  .sidebar-wrapper .sidebar-brand > a,
  .sidebar-wrapper .sidebar-dropdown > a:after,
  .sidebar-wrapper .sidebar-menu .sidebar-dropdown .sidebar-submenu li a:before,
  .sidebar-wrapper ul li a i,
  .page-wrapper .page-content,
  .sidebar-wrapper .sidebar-search input.search-menu,
  .sidebar-wrapper .sidebar-search .input-group-text,
  .sidebar-wrapper .sidebar-menu ul li a,
  #show-sidebar,
  #close-sidebar {
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
  }
  
  /*----------------page-wrapper----------------*/
  
  .page-wrapper {
    height: 100vh;
  }
  
  .page-wrapper .theme {
    width: 40px;
    height: 40px;
    display: inline-block;
    border-radius: 4px;
    margin: 2px;
  }
  
  .page-wrapper .theme.chiller-theme {
    background: #1e2229;
  }
  
  /*----------------sidebar-wrapper----------------*/
  
  .sidebar-wrapper {
    border-right: 1px solid #ccc;
  }
  
  .sidebar-wrapper ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .sidebar-wrapper a {
    text-decoration: none;
    color: #333;
    &.active {
      color: #aaa;
    }
  }
  
  /*----------------sidebar-content----------------*/
  
  .sidebar-content {
    // max-height: calc(100% - 30px);
    // height: calc(100% - 30px);
    overflow-y: auto;
    position: relative;
  }
  
  .sidebar-content.desktop {
    overflow-y: hidden;
  }
  
  /*--------------------sidebar-brand----------------------*/
  
  .sidebar-wrapper .sidebar-brand {
    padding: 10px 20px;
    display: flex;
    align-items: center;
  }
  
  .sidebar-wrapper .sidebar-brand > a {
    text-transform: uppercase;
    font-weight: bold;
    flex-grow: 1;
  }
  
  .sidebar-wrapper .sidebar-brand #close-sidebar {
    cursor: pointer;
    font-size: 20px;
  }
  /*--------------------sidebar-header----------------------*/
  
  .sidebar-wrapper .sidebar-header {
    padding: 20px;
    overflow: hidden;
  }
  
  .sidebar-wrapper .sidebar-header .user-pic {
    float: left;
    width: 60px;
    padding: 2px;
    border-radius: 12px;
    margin-right: 15px;
    overflow: hidden;
  }
  
  .sidebar-wrapper .sidebar-header .user-pic img {
    object-fit: cover;
    height: 100%;
    width: 100%;
  }
  
  .sidebar-wrapper .sidebar-header .user-info {
    float: left;
  }
  
  .sidebar-wrapper .sidebar-header .user-info > span {
    display: block;
  }
  
  .sidebar-wrapper .sidebar-header .user-info .user-role {
    font-size: 12px;
  }
  
  .sidebar-wrapper .sidebar-header .user-info .user-status {
    font-size: 11px;
    margin-top: 4px;
  }
  
  .sidebar-wrapper .sidebar-header .user-info .user-status i {
    font-size: 8px;
    margin-right: 4px;
    color: #5cb85c;
  }
  
  /*-----------------------sidebar-search------------------------*/
  
  .sidebar-wrapper .sidebar-search > div {
    padding: 10px 20px;
  }
  
  /*----------------------sidebar-menu-------------------------*/
  
  .sidebar-wrapper .sidebar-menu {
    padding-bottom: 10px;
  }
  
  .sidebar-wrapper .sidebar-menu .header-menu span {
    font-weight: bold;
    font-size: 14px;
    padding: 15px 20px 5px 20px;
    display: inline-block;
  }
  
  .sidebar-wrapper .sidebar-menu ul li a {
    display: inline-block;
    width: 100%;
    text-decoration: none;
    position: relative;
    padding: 8px 30px 8px 20px;
  }
  
  .sidebar-wrapper .sidebar-menu ul li a i {
    margin-right: 10px;
    font-size: 12px;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    border-radius: 4px;
  }
  
  .sidebar-wrapper .sidebar-menu ul li a:hover > i::before {
    display: inline-block;
    animation: swing ease-in-out 0.5s 1 alternate;
  }
  
  .sidebar-wrapper .sidebar-menu .sidebar-dropdown > a:after {
    display: inline-block;
    content: "";
    vertical-align: -.125em;
    background-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23333'><path fill-rule='evenodd' d='M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z'/></svg>");
    background-repeat: no-repeat;
    background-size: 1rem 1rem;
    text-align: center;
    position: absolute;
    right: 15px;
    top: 12px;
    height: 1rem;
    width: 1rem;
  }


  
  .sidebar-wrapper .sidebar-menu .sidebar-dropdown .sidebar-submenu ul {
    padding: 5px 0;
  }
  
  .sidebar-wrapper .sidebar-menu .sidebar-dropdown .sidebar-submenu li {
    padding-left: 25px;
    font-size: 13px;
  }
  
  .sidebar-wrapper .sidebar-menu .sidebar-dropdown .sidebar-submenu li a:before {
    display: inline-block;
    content: "";
    vertical-align: -.125em;
    background-image: url("data:image/svg+xml,<svg viewBox='0 0 16 16' fill='%23333' xmlns='http://www.w3.org/2000/svg'><path fill-rule='evenodd' d='M8 9.5a1.5 1.5 0 1 0 0-3 1.5 1.5 0 0 0 0 3z' clip-rule='evenodd'/></svg>");
    background-repeat: no-repeat;
    background-size: 1rem 1rem;
  }
  
  .sidebar-wrapper .sidebar-menu ul li a span.label,
  .sidebar-wrapper .sidebar-menu ul li a span.badge {
    float: right;
    margin-top: 8px;
    margin-left: 5px;
  }
  
  .sidebar-wrapper .sidebar-menu .sidebar-dropdown .sidebar-submenu li a .badge,
  .sidebar-wrapper .sidebar-menu .sidebar-dropdown .sidebar-submenu li a .label {
    float: right;
    margin-top: 0px;
  }
  
  .sidebar-wrapper .sidebar-menu .sidebar-submenu {
    display: none;
  }
  
  .sidebar-wrapper .sidebar-menu .sidebar-dropdown.active > a:after {
    transform: rotate(90deg);
    right: 17px;
  }
  
 
  // sidebarend


  // button
  .btn-rounded {
    border-radius: 10rem;
  }

  .btn-purple {
    background: #7d348e;
    color: #fff;
  }
  .btn-purple-outline {
    border: 1px solid #7d348e;
    color: #7d348e;
  }
  .btn-long {
    width: 200px;
    max-width: 100%;
  }
  // button end