.checkout {
    .stepper {
        width: 400px;
        margin: 50px auto;
        position: relative;
        padding: 0;
        list-style: none;
        overflow: hidden;
        -webkit-transition: height 0.2s ease-in-out;
        transition: height 0.2s ease-in-out;
        display: flex;
        justify-content: space-between;
        .stepper-step {
            -webkit-box-flex: 1;
            -ms-flex: auto;
            flex: auto;
            height: 4.5rem;
        }
        .stepper-head {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;
            text-decoration: none;
            color: unset;
            line-height: 1.3;
            cursor: pointer;
        }
        .stepper-step:first-child .stepper-head {
            padding-left: 1.5rem;
        }
        .stepper-content {
            position: absolute;
            width: 100%;
            padding: 0 1rem;
        }
        .stepper-active .stepper-content {
            display: block;
        }
        .stepper-content {
            left: 0;
        }
        .stepper-head-icon {
            margin: 1.5rem 0.5rem 1.5rem 0;
        }
        .stepper-active .stepper-head-icon {
            background-color: #1266f1;
        }
        .stepper-step:not(:last-child) .stepper-head:after {
            -webkit-box-flex: 1;
            -ms-flex: 1;
            flex: 1;
            height: 1px;
            width: 100%;
            margin-left: 0.5rem;
            content: "";
            background-color: rgba(0, 0, 0, 0.1);
        }
        .stepper-head-icon {
            -webkit-box-pack: center;
            -ms-flex-pack: center;
            justify-content: center;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;
            color: #fff;
            background-color: grey;
            border-radius: 100%;
            font-size: 0.875rem;
            width: 1.5rem;
            height: 1.5rem;
        }
        .stepper-head-icon,
        .stepper-head-text:after {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
        }
        .stepper-active .stepper-head-text {
            font-weight: 500;
        }
        .stepper-head-text:after {
            font-size: 0.8rem;
            content: attr(data-content);
        }
    }

    .coupon-btn {
        font-size: 18px;
        color: #7d348e;
        padding: 0;
        &:hover,
        &:active {
            color: #7d348e;
            background: transparent;
        }
    }
    .order-list-container {
        display: grid;
        grid-template-columns: 40px 90px auto 90px 110px 30px;
        grid-column-gap: 10px;
    }
    .order-list-container2 {
        display: grid;
        grid-template-columns: 90px auto 90px 110px;
        grid-column-gap: 10px;
    }
    .order-list {
        border-bottom: 1px solid #959595;
        padding-bottom: 15px;
        margin-bottom: 15px;
        .btn-link {
            color: rgb(149, 149, 149);
            &:active, &:hover, &:visited {
                color: rgb(149, 149, 149);
            }
        }
    }
    .shipping {
        margin-top: 50px;
        .custom-select {
            width: 50%;
            display: block;
            margin-bottom: 15px;
        }
    }
    .coupon {
        display: grid;
        grid-template-columns: 100px auto 30px;
        grid-column-gap: 10px;
        margin-bottom: 15px;
    }
    .step1 {
        display: grid;
        min-height: 500px;
        grid-gap: 60px;
        grid-template-columns: auto 370px;
    }
    .step2 .content {
        display: grid;
        grid-gap: 60px;
        grid-template-columns: auto 370px;
    }
    .step2 .border-line {
        border: 1px solid #e5e5e5;
        padding: 18px;
    }
    .link-btn {
        border: 0;
        background: transparent;
        color: #111;
    }
    .payment {
        margin-bottom: 30px;
        font-size: 14px;
    }
    @media screen and (min-width: 1200px) {
        .checkout-container {
            width: 1200px;
            margin: 30px auto;
        }
    }
}
